<template>
  <v-card class="things-container">
    <v-row>
      <v-col>
        <v-btn
          class="mx-2"
          fab
          dark
          color="indigo"
          @click="handleAddArticle"
        >
          <v-icon dark>
            mdi-plus
          </v-icon>
        </v-btn>
      </v-col>
      <v-col v-if="pageLength > 1">
        <v-pagination
          v-model="page.no"
          :length="pageLength"
          :total-visible="7"
          @input="handlePageNo"
        />
      </v-col>
    </v-row>
    <v-list two-line>
      <v-list-item
        v-for="item in things"
        :key="item.id"
        link
        :to="'/article/'+item.id"
      >
        <v-list-item-content class="text-left">
          <v-list-item-title>{{ item.title }}</v-list-item-title>
          <v-list-item-subtitle>{{ item.subTitle }}</v-list-item-subtitle>
        </v-list-item-content>
      </v-list-item>
    </v-list>
  </v-card>
</template>

<script>
import { list } from '@/api/modules/article'

export default {
  name: 'ArticleList',
  data: () => ({
    things: [],
    page: {
      no: 1,
      size: 10,
      total: 0
    }
  }),
  computed: {
    pageLength() {
      const result = Math.ceil(this.page.total / this.page.size)
      console.log(result)
      return result
    }
  },
  mounted() {
    this.initData()
  },
  methods: {
    handlePageNo() {
      this.initData()
    },
    initData() {
      list({
        pageNo: this.page.no,
        pageSize: this.page.size
      }).then(data => {
        this.page.total = data.data.total
        this.things = data.data.data
      })
    },
    handleAddArticle() {
      this.$router.push('/article/edit')
    }
  }
}
</script>

